<template>
    <div class="order">
        <div class="order_box">
            <div class="scroll_area">
                <div v-if="isOut" class="advice">
                    <div v-if="isFull" class="advice_text">建议你在<span>厂外等候</span></div>
                    <div v-if="!isFull" class="advice_text">厂内当前停车资源：<span>{{list.AREA_AMOUNT_USED}}/{{list.AREA_AMOUNT_TOTAL}}</span></div>
                </div>
                <div v-else-if="isPark" class="advice">
                    <div class="advice_text"> 请前往<span>{{list.AREA_NAME}}</span>等候</div>
                </div>
                <div v-else-if="isUnloading && this.isShow" class="advice">
                    <div class="advice_text">
                        请到<span>{{list.LINE_NAME}}-{{list.CUR_PLATFORM_NAME}}</span>卸货<br />停靠扫码
                    </div>
                </div>
                <div v-else-if="isUnloading && !this.isShow" class="advice">
                    <div class="advice_text"><span>卸货中</span>，扫码后离开平台</div>
                </div>
                <div v-else-if="isFinish" class="advice">
                    <div class="advice_text">卸货完毕，请<span>及时离场</span></div>
                </div>
                <div v-if="!isFinish" class="wait">
                    <p v-if="!isOut" class="num">
                        前面还有<span v-if="isWait">{{list.WAITTING_AMOUNT}}</span>
                        <span v-if="!isWait">0</span>人等待
                    </p>
                    <div class="wait_box" :class={on:isPark}>
                        <div class="park park1" :class={out_bottom:isOut}>
                            <div class="park_left">
                                <span class="iconfont park_icon">&#xe60a;</span>
                                <p>驻场</p>
                            </div>
                            <div class="park_right">
                                <div class="place">
                                    <div>位置：{{list.AREA_NAME}}</div>
                                    <div>
                                        <span>{{list.AREA_AMOUNT_USED}}</span>/<em>{{list.AREA_AMOUNT_TOTAL}}</em></div>
                                </div>
                                <div class="time">预计等待：{{list.WAITTING_DURATION?formatSeconds(list.WAITTING_DURATION):"--"}}</div>
                            </div>
                        </div>
                    </div>
                    <div class="wait_box" :class={on:isUnloading}>
                        <div class="park">
                            <div class="park_left">
                                <span class="iconfont park_icon">&#xe74d;</span>
                                <p>卸货</p>
                            </div>
                            <div class="park_right">
                                <div class="place">区域：{{list.LINE_NAME}}</div>
                                <div class="time">
                                    <!-- 可以去的所有平台 -->
                                    <div v-if="this.list.PLATFORMS && !isUnloading">平台：{{areaList}}</div>
                                    <!-- 当前要去的平台 -->
                                    <div v-if="this.list.PLATFORMS && isUnloading">平台：{{list.CUR_PLATFORM_NAME}}</div>
                                    <div v-if="this.list.PLATFORMS"><span>{{otherNumber}}</span>/<em>{{list.PLATFORM_AMOUNT}}</em></div>   
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- 卸货信息列表 -->
                <div class="unload_msg">
                    <table v-if="aList.length>1">
                        <tr>
                            <td>停靠平台</td>
                            <td>停靠时间</td>
                            <td>离开时间</td>
                        </tr>
                        <tr v-for="item in aList" :key="item.PLATFORM_CODE">
                            <td>{{item.origin[0].PLATFORM_NAME}}</td>
                            <td>{{item.origin.length>0?getShortTime(item.origin[0].OPERATE_TIME):"--"}}</td>
                            <td>{{item.origin.length>1?getShortTime(item.origin[1].OPERATE_TIME):"--"}}</td>
                        </tr>
                    </table>
                </div>
                <!-- <div v-if="isFinish" class="">
                    <p>空容器信息</p>
                    <p></p>
                </div> -->
            </div>
        </div>
        <div v-if="isOut || isUnloading" class="btn_box">
            <div v-if="isOut" class="scan_btn red" @click="back">继续装载，返回上一步</div>
            <div v-if="isUnloading">
                <div v-if="this.isShow" class="scan_btn" @click="arrive('0')">已停靠，扫描二维码</div>
                <div v-if="!this.isShow" class="scan_btn red" @click="arrive('1')">卸货完成，离开平台扫码</div>
            </div>
        </div>
        <div class="pop" v-if="popShow || isLeave">
			<div class="pop_con">
				<div v-if="!isError" class="pop_info">
                    <span class="iconfont true_icon">&#xe60b;</span>
                    <p v-if="isLeave">您已离厂，该次配送任务结束。</p>
                    <p class="num">
                        车牌号：<span>{{dList.CAR_PLATE_NO}}</span>
                    </p>
                    <div v-if="isUnloading && !isLeave">
                        <p v-if="isStart && !hasTimes" class="num">开始卸货</p>
                        <p v-if="!isStart && !hasTimes" class="num">完成卸货，现可离场</p>
                        <p v-if="hasTimes" class="num">还有卸货次数，请继续卸货</p>
                    </div>
                </div>
                <div v-if="isError" class="pop_info">
                    <span class="iconfont error_icon">&#xe712;</span>
                    <p>{{this.errorText}}</p>
                </div>
				<div class="confirm_box">
					<div v-if="!isLeave" @click="be_sure" class="confirm">确定</div>
					<div v-if="isLeave" @click="leave_sure" class="confirm">确定</div>
				</div>
			</div>
			<div class="mask"></div>
		</div>
    </div>
</template>
<script>
import Qs from 'qs'
import Vue from 'vue'
import {mapState} from 'vuex'
export default {
    name:'Order',
    data(){
        return{
            popShow:false,
            getType:'',
            plat_no:'',
            hasTimes:false,
            isStart:true,
            isError:false,
            errorText:''
        }
    },
    props:{
        list:Object,
        dList:Object,
        mList:Object,
        aList:{
            type:Array,
            default:[]
        }
    },
    methods:{
        formatSeconds (time) {
            let min = Math.floor(time % 3600)
            let val = (Math.floor(time / 3600)?Math.floor(time / 3600) + '小时' : '') + Math.floor(min / 60) + '分钟'
            return val
        },
        back(){
            this.$router.push('/detail')
        },
        getShortTime(num){
            //return num.split(' ')[1].substring(0,5);
            return num.substring(5,16).replace(/-/,"/");
        },
        arrive:function(num){
            //微信扫码功能
			wx.scanQRCode({
				needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
				scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
				success: (res) => {
                    console.log(res)
                    this.plat_no=res.resultStr
                    let now_time=new Date().toLocaleString('chinese',{hour12:false});
                    let userName=this.$store.state.userName;
                    let data=Qs.stringify({
                        PLATFORM_NO:this.plat_no,
                        OPERATE_OPTION:num,
                        WAYBILL_CODE:Vue.localStorage.get('orderNum'),
                        CAR_PLATE_NO:Vue.localStorage.get('carNum'),
                        OPERATOR_NAME:userName,
                        OPERATOR_ID:this.Cookies.get('wxqy_userid'),
                        OPERAT_DATE:now_time
                    })
                    this.axios.post(this.api+'/lyapi/transfer/SCAN_PLATFORM_IN_OUT',data,this.sendHeaders).then(res=>{
                        console.log(res)
                        let data=res.data
                        if(data.RESULT==1){
                            this.getType=num
                            if(this.getType==0){
                                this.$store.commit('getShow',false)
                            }else{
                                this.$store.commit('getShow',true)
                                if(data.data.UNLOAD_AMOUNT==0){
                                    this.isStart=false
                                }else{
                                    this.hasTimes=true
                                }
                            }
                            this.popShow=true
                        }else{
                            this.errorText=data.MESSAGE
                            this.isError=true
                            this.popShow=true
                        }
                    })
					
				}
            })
            // if(1==1){
            //     this.getType=num
            //     if(this.getType==0){
            //         this.$store.commit('getShow',false)
            //     }else{
            //         this.$store.commit('getShow',true)
            //         if(true){
            //             this.isStart=false
            //         }else{
            //             this.hasTimes=true
            //         }
            //     }
            //     this.popShow=true
            // }else{
            //     this.errorText="出错唔"
            //     this.isError=true
            //     this.popShow=true
            // }
        },
        be_sure(){
            this.isStart=true
            this.isError=false
            this.hasTimes=false
            if(this.getType==1){
                this.$emit('showList')
                this.$emit('reMessage')
            }
             this.popShow=false
        },
        leave_sure(){
            Vue.localStorage.remove('carNum');
            Vue.localStorage.remove('WAYBILL_STATE');
            Vue.localStorage.remove('isShow');
            Vue.localStorage.remove('isArrive');
            Vue.localStorage.remove('orderNum');
            this.$router.push('/')
        }
    },
    computed:mapState({
        otherNumber:function(){
            let num=0
            let array=this.list.PLATFORMS
            for(let i=0;i<array.length;i++){
                if(array[i].PLATFORM_FREE==0){
                    num++
                }
            }
            return num
        },
        areaList:function(){
            let areaString=''
            let areaArray=[]
            let array=this.list.PLATFORMS
            for(let i=0;i<array.length;i++){
                if(array[i].PLATFORM_FREE==0){
                    areaArray.push(array[i].PLATFORM_NAME)
                    areaString=areaArray.join(",")
                }
            }
            return areaString
        },
        isOut:function(){
            return this.list.WAYBILL_STATE<3
        },
        isPark:function(){
            return this.list.WAYBILL_STATE==3 || this.list.WAYBILL_STATE==4
        },
        isUnloading:function(){
            return this.list.WAYBILL_STATE==5
        },
        isFinish:function(){
            return this.list.WAYBILL_STATE==6
        },
        isLeave:function(){
            return this.list.WAYBILL_STATE==7
        },
        isWait:function(){
            return this.list.WAYBILL_STATE<5
        },
        isHot:function(){
            return this.list.LEVEL_URGENT==1
        },
        isLater:function(){
            return this.list.LEVEL_LATE==1
        },
        isFull:function(){
            return this.list.AREA_AMOUNT_TOTAL==this.list.AREA_AMOUNT_USED
        },
        isShow:function(state){
			if(state.isShow===true && Vue.localStorage.get('isShow')!==null){
				state.isShow=JSON.parse(Vue.localStorage.get('isShow'))
			}
			return state.isShow
		}
    })
}
</script>
<style lang="stylus" scoped>
.order
    padding-bottom 1.88rem
    flex 1
    overflow-x hidden
    .order_box
        padding 0 0.3rem
        .unload_msg
            table
                margin-top 0.2rem
                border none
                background #fff
                tr
                    td
                        border-color #ccc
                        padding 0.2rem 0
                tr:first-child
                    td
                        font-weight bold
                        border-color #999
        .advice
            display table
            width 100%
            height 1.5rem
            background #fff
            text-align center
            font-size 0.36rem
            color #363636
            font-weight bold
            letter-spacing 1px
            margin-top 0.16rem
            .advice_text
                display table-cell
                vertical-align middle
                font-size 0.5rem
            span    
                color #cf001f
        .wait
            font-size 0.42rem
            color #363636
            background #fff
            border-radius 0.1rem
            margin-top 0.25rem
            overflow hidden
            .num 
                height 0.9rem
                line-height 0.9rem
                text-align center
                border-bottom 1px solid #eaeaea
                span 
                    font-size 0.44rem
                    font-weight bold
                    margin 0 0.06rem
            .wait_box
                .park
                    display flex
                    align-items center
                    color #979797
                    background rgba(228,228,228,0.9)
                    .park_left
                        text-align center
                        padding 0 0.26rem
                        font-size 0.36rem
                        font-weight bold
                        .park_icon
                            font-size 0.6rem
                            color #979797   
                            margin-bottom 0.06rem
                    .park_right
                        flex 1
                        div 
                            font-size 0.36rem
                        .place
                            display flex
                            align-items center
                            justify-content space-between
                            padding 0.2rem 0 0.1rem
                            border-bottom 1px solid #ccc
                            padding-right 0.2rem
                            span    
                                font-size 0.44rem
                                font-weight bold
                        em 
                            font-style normal
                            font-size 0.3rem
                            font-weight normal
                        .time
                            padding 0.1rem 0 0.2rem
                            display flex
                            align-items center
                            justify-content space-between 
                            padding-right 0.2rem
                            span    
                                font-size 0.44rem
                                font-weight bold
                .park.out_bottom
                    border-bottom 1px solid #ccc
            .wait_box.on
                .park
                    color #363636
                    background #fff
                    font-weight bold
                    border 0.1rem solid #cf001f
                    border-radius 0.1rem
                    .park_icon
                        color #363636
                    .place
                        border-color #eaeaea
        .wait_out
            .wait_box
                .park
                    color #363636
                    background #fff
                    .park_left
                        .park_icon
                            color #363636
                    .park_right
                        .place
                            border-color #eaeaea 
                .park1
                    border-bottom 1px solid #eaeaea
    .btn_box
        position: fixed;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 0.4rem 0.3rem;
        z-index 10
        background #f3f3f3
        .scan_btn
            height: 1rem;
            line-height: 1rem;
            color: #fff;
            background-color: #FF841E;
            font-size: 0.5rem;
            font-weight: bold;
            border-radius: 0.1rem;
            margin: 0 auto;
            text-align center
        .scan_btn.red 
            background #cf001f

</style>


