<template>
    <div class="list">
        <p class="title">信息</p>
        <div class="list_main">
            <div v-for="item in list" class="item">
                <div class="text">
                    <span class="des">{{item.title}}</span>
                    <span v-if="item.isLate==1?true:false" class="iconfont warn">&#xe623;</span>
                    <span v-if="item.isUrgent==1?true:false" class="iconfont hot_icon">&#xe617;</span>
                </div>
                <div class="time">{{item.createDate}}</div>
                <!-- <span v-if="false" class="iconfont point">&#xe600;</span> -->
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:'List',
    props:{
        list:Array
    },
    data(){
        return{
        }
    }
}
</script>
<style lang="stylus" scoped>
    .list
        .title  
            font-size 0.28rem
            color #cf001f
            height 0.84rem
            line-height 0.84rem
            background #fff 
            text-indent 0.5rem
        .list_main
            padding 0 0.3rem 0.2rem
            .item
                position relative
                display flex
                justify-content space-between
                height 1rem
                line-height 1rem
                background #fff 
                border 1px solid #eaeaea
                border-radius 0.1rem
                font-size 0.28rem
                color #363636
                padding 0 0.18rem
                margin-top 0.2rem
                .text 
                    .des
                        font-weight bold
                        vertical-align top
                    .hot_icon
                        vertical-align top
                    
                .point 
                    position absolute
                    right 0
                    top 50% 
                    transform:translateY(-50%) 
                .time 
                    font-size 0.24rem
                    color #7c7c7c
    .warn,.hot_icon
        font-size 0.4rem
                
</style>


