<template>
    <div class="process">
        <vue-element-loading :active="isShowMask" :is-full-screen="true" />
        <home-header ref="fromChild"></home-header>
        <process-basic :list="orderList" :dList="detailList"></process-basic>
        <process-order :list="orderList" :dList="detailList" :mList="messageList" :aList="arrayList" @showList="getProcessList" @reMessage="childMethod"></process-order>
    </div>
</template>
<script>
import HomeHeader from '@/common/Header'
import ProcessOrder from './components/Order'
import ProcessBasic from './components/Basic'
import VueElementLoading from 'vue-element-loading'
import Vue from 'vue'
import Qs from 'qs'
export default {
    name:'Process',
    components:{
        HomeHeader,
        ProcessOrder,
        ProcessBasic,
        VueElementLoading
    },
    data(){
        return{
            orderList:{},
            detailList:{},
            messageList:{},
            arrayList:[],
            timer:'',
            isShowMask:true
        }
    },
    methods:{
        // getDetail(){
        //     this.axios.post('/detail/query').then(res=>{
        //         if(res.data.RESULT==1){
        //             let data=res.data.data
        //             this.detailList=data
        //         }
        //     })
        // },
        // getProcessList(){
        //     this.axios.post('/process/query').then(this.getProcessListSucc)
        // },
        getDetail(){
            let param_json={
				'CAR_PLATE_NO':Vue.localStorage.get('carNum'),
				'WAYBILL_CODE':Vue.localStorage.get('orderNum')
			}
            let myData=Qs.stringify(param_json)
            this.axios.post(this.api+'/lyapi/transfer/ORDER_DETAIL',myData,this.sendHeaders).then(res=>{
                if(res.data.RESULT==1){
                    let data=res.data.data
                    this.detailList=data
                }
            })
        },
        getProcessList(){
            let param_json={
				'CAR_PLATE_NO':Vue.localStorage.get('carNum'),
				'WAYBILL_CODE':Vue.localStorage.get('orderNum')
			}
            let myData=Qs.stringify(param_json)
            let qyUserid=this.Cookies.get('wxqy_userid')
            this.axios.post(this.api+'/lyapi/transfer/DETAIL_LINE_UP',myData,this.sendHeaders).then(this.getProcessListSucc)
        },
        getProcessListSucc(res){
            let process_info=res.data
            if(process_info.RESULT==1){
                let data=process_info.data
                this.orderList=data
                if(data.WAYBILL_STATE>1){
                    Vue.localStorage.set('WAYBILL_STATE',data.WAYBILL_STATE);
                }
                //卸货信息排序
                let beforeData=data.TASKS
                let afterData=[]
                let tempArr=[]
                for (let i = 0; i < beforeData.length; i++) {
                    if (tempArr.indexOf(beforeData[i].PLATFORM_CODE) === -1) {
                        afterData.push({
                            PLATFORM_CODE: beforeData[i].PLATFORM_CODE,
                            origin: [beforeData[i]]
                        })
                        tempArr.push(beforeData[i].PLATFORM_CODE)
                    } else {
                        for (let j = 0; j < afterData.length; j++) {
                            if (afterData[j].PLATFORM_CODE == beforeData[i].PLATFORM_CODE) {
                                afterData[j].origin.push(beforeData[i])
                                break
                            }
                        }
                    }
                }
                function compare(property){
                    return function(a,b){
                        var value1=a[property]
                        var value2=b[property]
                        return value1-value2
                    }
                }
                for(let i=0;i<afterData.length;i++){
                    afterData[i].origin.sort(compare('OPERATE_CODE'))
                }
                this.arrayList=afterData
                console.log(this.arrayList)
            }
            this.isShowMask=false 
        },
        childMethod(){
            this.$refs.fromChild.getNews()
        }
    },
    mounted(){
        //配置微信JSSDK参数
        this.common.getsign()
        this.getDetail()
        this.getProcessList()
        this.timer = setInterval(this.getProcessList, 60000);
    },
    beforeDestroy() {
        clearInterval(this.timer);
    }
    
}
</script>
<style lang="stylus" scoped>
    .process
        display flex 
        flex-direction column 
        height 100%
</style>


