<template>
    <div class="info">
        <div class="line">
            <p>
                <span>要求到达时间：</span>
                <span>{{list.PER_ONLINE_DATE?list.PER_ONLINE_DATE:"--"}}</span>
            </p>
            <p>
                <span>紧急度：</span>
                <span>{{list.LEVEL_URGENT?(list.LEVEL_URGENT==1?"加急":"正常"):""}}</span>
            </p>
            <!-- <p>
                <span>出发地：</span>
                <span>{{list.START_PLACE}}</span>
            </p> -->
            <p>
                <span>目的地：</span>
                <span>{{list.TARGET_PLACE}}</span>
            </p>
        </div>
        <div class="line">
            <p>
                <span>承运商：</span>
                <span>{{list.CARRIER_NAME}}</span>
            </p>
            <p>
                <span>车牌号码：</span>
                <span>{{list.CAR_PLATE_NO}}</span>
            </p>
            <p>
                <span>司机：</span>
                <span>{{list.DRIVER}}</span>
            </p>
        </div>
    </div>
</template>
<script>
export default {
    name:'Info',
    props:{
        list:Object
    }
}
</script>
<style lang="stylus" scoped>
    .info
        padding 0 0.3rem
        font-size 0.28rem
        color #363636
    .line
        border-bottom 1px solid #eaeaea
        padding 0.2rem 0
        p   
            font-size 0.38rem
            line-height 0.65rem
        .order
            font-weight bold
</style>


