<template>
    <div class="scan_box">
		<div class="scan">
			<div class="title">执行配送任务</div>
			<div class="con">
				<span class="iconfont wran">&#xe981;</span>
				<div class="text1">绑定运单</div>
				<div class="text2">请扫描配送车辆二维码</div>
				<div class="start_box">
					<div @click="start" class="start">绑定车牌，开始配送</div>
				</div>
			</div>
		</div>
		<!-- <div class="scan">
			<div class="title">运返空容器/其他特殊情况</div>
			<div class="con">
				<span class="iconfont wran orange">&#xe981;</span>	
				<div class="text1 text1_other">平台扫码</div>
				<div class="text2">停靠、离开平台，请平台扫码</div>
				<div class="btn_box">
					<div class="stop btn">停靠平台</div>
					<div class="leave btn">离开平台</div>
				</div>
			</div>
		</div> -->
		<div class="pop" v-if="popShow">
			<div v-if="!isError" class="pop_con">
				<div class="pop_info">
					<span class="iconfont true_icon">&#xe60b;</span>
                    <p>绑定成功</p>
                    <p class="num">
                        车牌号：<span>{{getNum}}</span>
                    </p>
                </div>
				<div class="confirm_box">
					<div @click="skip_detail" class="confirm">确定</div>
				</div>
			</div>
			<div v-if="isError" class="pop_con">
				<div class="pop_info">
                    <span class="iconfont error_icon">&#xe712;</span>
                    <p>{{this.errorText}}</p>
                </div>
				<div class="confirm_box">
					<div @click="hideBox" class="confirm">确定</div>
				</div>
			</div>
			<div class="mask"></div>
		</div>
	</div>
</template>
<script>
import Vue from 'vue'
import Qs from 'qs'
export default {
	name: 'Scan',
	data(){
		return{
			popShow:false,
			getNum:'',
			getOrder:'',
			isError:false,
			errorText:''
		}
	},
	methods:{
		start(){
			//微信扫码功能
			wx.scanQRCode({
				needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
				scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
				success: (res) => {
					console.log(res)
					this.getNum = res.resultStr; // 获取扫码车牌号
					let now_time=new Date().toLocaleString('chinese',{hour12:false});
					let userName=this.$store.state.userName;
					let qyUserid=this.Cookies.get('wxqy_userid')
					let data=Qs.stringify({
						CAR_PLATE_NO:this.getNum,
						OPERATOR_NAME:userName,
						OPERATOR_ID:qyUserid,
						OPERAT_DATE:now_time
					})
					this.axios.post(this.api+'/lyapi/transfer/BIND_CAR_PLATE',data,this.sendHeaders).then(res=>{
						console.log(res)
						let data=res.data
						if(data.RESULT==1){
							this.getOrder=data.data.WAYBILL_CODE
							Vue.localStorage.set('carNum',this.getNum);
							Vue.localStorage.set('orderNum',this.getOrder);
							this.popShow=true
						}else{
							this.errorText=data.MESSAGE
							this.isError=true
							this.popShow=true
						}
					})
				}
			})
		},
		skip_detail(){
			this.$router.replace({
				path:'/detail'
			})
		},
		hideBox(){
			this.popShow=false
			this.isError=false
		}
	}
}
</script>
<style lang="stylus" scoped>
	.scan_box
		padding 0 0.3rem
		.scan
			background #fff
			margin-top 0.2rem
			border 1px solid #eaeaea
			border-radius 0.2rem
			.title
				height 1rem
				line-height 1rem
				font-size 0.38rem
				color #363636
				text-align center
				font-weight bold
				border-bottom 1px solid #eaeaea
			.con
				text-align center
				padding 0.28rem 0 0.48rem 0
				.wran
					color #cf001f
					font-size 2rem
					margin-bottom 0.28rem
				.orange
					color #ea890d
				.text1
					font-size 0.4rem
					color #cf001f
					font-weight bolder
					margin-bottom 0.22rem
				.text1_other
					color #eea03c
				.text2
					font-size 0.38rem
					color #363636
					margin-bottom 0.44rem
				.start_box
					padding 0 0.3rem
					.start
						height 1rem
						line-height 1rem
						color #fff
						background-color #00c97a
						font-size 0.5rem
						font-weight bold
						border-radius 0.1rem
						margin 0 auto
				.btn_box
					padding 0 0.3rem
					display flex
					justify-content space-between
					.btn
						width 48%
						height 0.88rem
						line-height 0.88rem
						color #fff
						font-size 0.44rem
						font-weight bold
						border-radius 0.1rem
						background-color #cf001f
					.stop
						background-color #ea890d
</style>


