<template>
	<div class="header clearfix">
		<span class="img_span">
			<img class="header_img" :src="this.userImg" alt="">
		</span>
		<div class="user_info">
			<div class="name">{{this.userName}}</div>
			<div class="job">{{this.userJob}}</div>
		</div>
		<div class="header_right">
			<div class="right_icon">
				<span @click="to_freshen" class="freshen">
					<img src="@/assets/images/freshen_icon.png" />
				</span>
				<span @click="to_message" class="message">
					<img src="@/assets/images/msg_icon.png" />
					<span v-if="isNew" class="iconfont point">&#xe600;</span>
				</span>
			</div>
		</div>
	</div>
</template>

<script>
import Vue from 'vue'
import {mapState} from 'vuex'
export default {
	name: 'Header',
	data(){
		return{
			isNew:false,
			timer:'',
			beforeDate:0
		}
	},
	methods:{
		to_message(){
			this.$router.push({
				path:'/message'
			})
		},
		to_freshen(){
			this.$router.go(0)
		},
		getNews(){
			let qyUserid=this.Cookies.get('wxqy_userid')
			// this.axios.get('/message/list').then(res=>{
			
			// this.axios.get(this.api+'/lyapi/notifyMessageRecord/'+'420621197301211',{
            //     params:{code:'20201019HBA1019A0030427'}
            // }).then(res=>{

			this.axios.get(this.api+'/lyapi/notifyMessageRecord/'+qyUserid,{
				params:{code:Vue.localStorage.get('orderNum')}
			}).then(res=>{
				console.log(res)
				let msgData=res.data;
				if(msgData.status==1){
					let msgList=msgData.data.NOTIFY_MESSAGE
					//列表提示信息
					let con=0
					for(var i=0;i<msgList.length;i++){
						if(msgList[i].isRead==0){
							this.isNew=true
							con=1
							return
						}
					}
					if(con==0){
						this.isNew=false
					}
				}else{
					console.log(res.message)
				}
			})
		}
	},
	//computed:mapState(['userName','userJob','userImg']),
	//刷新的时候，state的值初始化为空，需从localStorage重新获得
	computed:mapState({
		userName:function(state){
			if(state.userName==''){
				state.userName=Vue.localStorage.get('userName')
			}
			return state.userName
		},
		userJob:function(state){
			if(state.userJob==''){
				state.userJob=Vue.localStorage.get('userJob')
			}
			return state.userJob
		},
		userImg:function(state){
			if(state.userImg==''){
				state.userImg=Vue.localStorage.get('userImg')
			}
			return state.userImg
		}
	}),
	mounted(){
		//是否有新信息
		this.getNews()
		this.timer = setInterval(this.getNews, 60000);
    },
    beforeDestroy() {
        clearInterval(this.timer);
    }
}
</script>

<style lang="stylus" scoped>
	.header
		display table
		width 100%
		height 1.2rem
		border-bottom 1px solid #ebebeb
		background #fff
		.img_span 
			display table-cell
			vertical-align middle
			width 0.95rem
			padding-left 0.3rem
			padding-right 0.28rem
			.header_img
				display inline-block
				width 0.95rem
				height 0.95rem
				border-radius 50%
				overflow hidden
		.user_info
			display table-cell
			vertical-align middle
			.name
				font-size 0.3rem
				color #1f1e1e
				font-weight bold
				margin-bottom 0.12rem
			.job
				font-size 0.24rem
				color #7c7c7c
		.header_right
			position relative
			display table-cell
			vertical-align middle
			padding-right 0.3rem
			.right_icon
				float right
				.freshen
					width 0.72rem
					height 0.72rem
					vertical-align middle
					display inline-block
					margin-right 0.2rem
					img 
						width 100%
						height 100%
				.message
					width: 0.72rem;
					height: 0.72rem;
					vertical-align: middle;
					display: inline-block;
					img
						width 100%
						height 100%
		
</style>



