import Vue from 'vue'
import axios from 'axios'
import Qs from 'qs'
//配置微信JSSDK
function getsign(){
    //onsole.log("getsign")
    let url=location.href.split("#")[0];
    //console.log(url)
    let data = Qs.stringify({url:url})
    axios.post('../../jsapi/config',data,{headers:{'Content-Type':'application/x-www-form-urlencoded'}}).then(res=>{
        console.log(res);
        let config=res.data;
        wx.config({
            debug: false, // 开启调试模式,
            appId: config.appId, // 必填，企业号的唯一标识，此处填写企业号corpid
            timestamp: config.timestamp, // 必填，生成签名的时间戳
            nonceStr: config.nonceStr, // 必填，生成签名的随机串
            signature: config.signature,// 必填，签名，见附录1
            jsApiList: ['scanQRCode'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
        })
    }, err => {
        console.log(err)
    })
}
export default {getsign}


