import Vue from 'vue'
import Vuex from 'vuex'
// 告诉 vue “使用” vuex
Vue.use(Vuex)
export default () => {
    return new Vuex.Store({
        state: {
            //应用初始化的数据
            //vueElementLoading: true, 
            userName:'',
            userJob:'',
            userImg:'',
            description:'',
            isNews:'',
            isShow:true,
            isArrive:true
        },
        mutations: { //修改state的方法
            // updateLoading(state, value) {
            //     state.vueElementLoading = value
            // },
            getName(state, value) {
                state.userName = value
                Vue.localStorage.set('userName', value)
            },
            getJob(state, value) {
                state.userJob = value
                Vue.localStorage.set('userJob', value)
            },
            getImg(state, value) {
                state.userImg = value
                Vue.localStorage.set('userImg', value)
            },
            getDes(state, value){
                state.description=value
                Vue.localStorage.set('description', value)
            },
            getNews(state,value){
                state.isNews=value
                //Vue.localStorage.set('isNews', value)
            },
            getShow(state,value){
                state.isShow=value
                Vue.localStorage.set('isShow', value)
            },
            getArrive(state,value){
                state.isArrive=value
                Vue.localStorage.set('isArrive', value)
            }
        }
    })
}


