<template>
    <div class="goods" :class="{on:!list.length}">
        <div class="box">
            <p>途经装货点</p>
            <table>
                <tr>
                    <!-- <th>编号</th> -->
                    <th>供应商简称</th>
                    <th>到达时间</th>
                    <th>离开时间</th>
                </tr>
                <tr v-for="item in list">
                    <!-- <td>{{item.PLACE_CODE}}</td> -->
                    <td>{{item.origin[0].PLACE_NAME}}</td>
                    <td>{{item.origin.length>0?getShortTime(item.origin[0].PLACE_TIME):"--"}}</td>
                    <td>{{item.origin.length>1?getShortTime(item.origin[1].PLACE_TIME):"--"}}</td>
                </tr>
            </table>
        </div>
    </div>
</template>
<script>
export default {
    name:'Goods',
    props:{
        list:Array
    },
    methods:{
        getShortTime(num){
            //return num.split(' ')[1].substring(0,5);
            return num.substring(5,16).replace(/-/,"/");
        }
    }
}
</script>
<style lang="stylus" scoped>
    .goods
        padding-bottom 0.3rem
        .box
            p 
                font-size 0.38rem
                padding 0.28rem 0
                font-weight bold
    .goods.on
        border-bottom: 1px solid #eaeaea;

</style>


