<template>
	<div class="home">
		<vue-element-loading :active="isShowMask" :is-full-screen="true" />
		<home-header></home-header>
		<home-scan></home-scan>
	</div>
</template>

<script>
import HomeHeader from '@/common/Header'
import HomeScan from './components/Scan'
import VueElementLoading from 'vue-element-loading'
import Vue from 'vue'
import Qs from 'qs'
export default {
	name: 'Home',
	components: {
		HomeHeader,
		HomeScan,
		VueElementLoading
	},
	data(){
		return{
			isShowMask:true
		}
	},
	methods:{
		getUserInfo(){
			let qyUserid=this.Cookies.get('wxqy_userid')
			console.log(qyUserid)
			this.axios.get(this.api+'/lyapi/userInfo/'+qyUserid).then(res=>{
				console.log(res)
				let userData=res.data;
				if(userData.status==1){
					let userList=userData.data
					this.$store.commit('getName',userList.name)
					this.$store.commit('getJob',userList.position)
					this.$store.commit('getImg',userList.avatar)
				}else{
					console.log(res.message)
				}
				this.isShowMask=false
			})
		}
	},
	created(){
		// Vue.localStorage.remove('carNum');
		// Vue.localStorage.remove('WAYBILL_STATE');
		// Vue.localStorage.remove('isShow');
		// Vue.localStorage.remove('isArrive');
		if(Vue.localStorage.get('WAYBILL_STATE')){
			//已经前往三厂
			this.$router.push('/process');
		}
		else if(Vue.localStorage.get('carNum')){
			//进入首页，如果当前已经绑定车牌号，跳转详情页
			this.$router.push('/detail');
		}
	},
	mounted(){
		console.log(new Date().toLocaleString('chinese',{hour12:false}))
		//配置微信JSSDK参数
		this.common.getsign()
		//获取用户信息
		this.getUserInfo()
	}
}
</script>

<style lang="stylus" scoped>
</style>



