<template>
    <div class="basic">
        <div class="status">
            <div class="num">{{dList.CAR_PLATE_NO}}</div>
            <div>
                <!-- <span v-if="isLater" class="iconfont warn">&#xe623;</span>
                <span v-if="isHot" class="iconfont hot_icon">&#xe617;</span> -->
                <span v-if="isLater" class="warn">已晚点</span>
                <span v-if="isHot" class="hot_icon">已加急</span>
            </div>
        </div>
        <div class="order_info" :class={on:isHot}>
            <div class="info1">
                流水号：<span>{{list.WAYBILL_CODE}}</span>
            </div>
            <div class="info2">
                <span v-if="dList.PER_ONLINE_DATE">要求到达时间：{{arriveTime}}</span>
                <span>{{dList.TARGET_PLACE}}</span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name:'basic',
    props:{
        list:Object,
        dList:Object
    },
    computed:{
        isHot:function(){
            return this.list.LEVEL_URGENT==1
        },
        isLater:function(){
            return this.list.LEVEL_LATE==1
        },
        arriveTime:function(){
            let getTime=this.dList.PER_ONLINE_DATE
            let timeResult=getTime.substring(0,getTime.length-3)
            return timeResult
        },
    }
}
</script>

<style lang="stylus" scoped>
.basic
    .status
        display flex
        justify-content space-between
        padding 0 0.3rem
        font-size 0.28rem
        height 0.82rem
        line-height 0.82rem
        background #fff
        .num 
            font-weight bold
        .hot   
            color #cf001f
        .warn,.hot_icon
            font-size 0.3rem
    .order_info
        background #fff
        font-size 0.28rem
        color #363636
        border-radius 0.1rem
        margin 0 0.3rem
        margin-top 0.18rem
        border 1px solid #eaeaea
        .hot_show
            display:none
        div
            height 0.8rem
            line-height 0.8rem
            padding 0 0.18rem
        .info1
            border-bottom 1px solid #eaeaea
            font-weight bold
        .info2
            display flex
            justify-content space-between
    .order_info.on 
        background #ffe8eb
        border 1px solid #cf001f
        .info1
            border-color #fff
</style>


