<template>
    <div>
        <vue-element-loading :active="isShowMask" :is-full-screen="true" />
        <home-header></home-header>
        <message-list :list="msgList"></message-list>
    </div>
</template>
<script>
import HomeHeader from '@/common/Header'
import MessageList from './components/List'
import VueElementLoading from 'vue-element-loading'
import Vue from 'vue'
export default {
    name:'Message',
    components:{
        HomeHeader,
        MessageList,
        VueElementLoading
    },
    data(){
        return{
            msgList:[],
            isShowMask:true
        }
    },
    mounted(){
        var qyUserid=this.Cookies.get('wxqy_userid')
        console.log(qyUserid)
        // this.axios.get(this.api+'/lyapi/notifyMessageRecord/'+'420621197301211',{
        //         params:{readFlag:true,code:'20201019HBA1019A0030427'}
        //     }).then(res=>{
        
        this.axios.get(this.api+'/lyapi/notifyMessageRecord/'+qyUserid,{
                params:{readFlag:true,code:Vue.localStorage.get('orderNum')}
            }).then(res=>{
			console.log(res)
            let msgData=res.data;
            if(msgData.status==1){
                this.msgList=msgData.data.NOTIFY_MESSAGE
            }else{
                console.log(res.message)
            }
            this.isShowMask=false
        })
        // this.axios.get('/message/list').then(res=>{
		// 	console.log(res)
        //     let msgData=res.data;
        //     if(msgData.status==1){
        //         this.msgList=msgData.data.NOTIFY_MESSAGE
        //     }else{
        //         console.log(res.message)
        //     }
        //     this.isShowMask=false 
		// })
    },
    beforeDestroy(){
        var qyUserid=this.Cookies.get('wxqy_userid')
        console.log("beforeDestroy")
        this.axios.get(this.api+'/lyapi/notifyMessageRecord/'+qyUserid,{
            params:{readFlag:true,code:Vue.localStorage.get('orderNum')}
        })
    }
}
</script>
<style lang="stylus" scoped>

</style>


