<template>
	<div class="detail">
		<vue-element-loading :active="isShowMask" :is-full-screen="true" />
		<home-header></home-header>
		<detail-order :list="infoList"></detail-order>
		<div class="main">
			<detail-info :list="infoList"></detail-info>
			<div class="table_box">
				<detail-goods :list="arrayList"></detail-goods>
				<detail-supplier :list="supplierList"></detail-supplier>
			</div>
		</div>
		<detail-btn @getTime="getHomeInfo"></detail-btn>
	</div>
</template>

<script>
import HomeHeader from '@/common/Header'
import DetailOrder from './components/Order'
import DetailInfo from './components/Info'
import DetailGoods from './components/Goods'
import DetailSupplier from './components/Supplier'
import DetailBtn from './components/Btn'
import VueElementLoading from 'vue-element-loading'
import Vue from 'vue'
import Qs from 'qs'

export default {
	name: 'Detail',
	components:{
		HomeHeader,
		DetailOrder,
		DetailInfo,
		DetailGoods,
		DetailSupplier,
		DetailBtn,
		VueElementLoading
	},
	data(){
		return{
			infoList:{},
			supplierList:[],
			arrayList:[],
			isShowMask:true
		}
	},
	methods:{
		getHomeInfo(){
			const vm=this
			let param_json={
				'CAR_PLATE_NO':Vue.localStorage.get('carNum'),
				'WAYBILL_CODE':Vue.localStorage.get('orderNum')
			}
			let myData=Qs.stringify(param_json)
			Promise.all([
				// this.axios.post('/detail/query',myData),
				// this.axios.post('/detail/goodsList',myData)
				this.axios.post(this.api+'/lyapi/transfer/ORDER_DETAIL',myData,this.sendHeaders),
				this.axios.post(this.api+'/lyapi/transfer/THE_WAY',myData,this.sendHeaders)
			]).then(res=>{
				console.log(res)
				let infoRes=res[0].data
				console.log(infoRes.RESULT)
				if(infoRes.RESULT==1){
					const data=infoRes.data
					vm.infoList=data
					vm.supplierList=data.order_list
				}
				let listRes=res[1].data
				if(listRes.RESULT==1){
					const data=listRes.data
					const beforeData=data.PLACES
					let afterData=[]
					let tempArr=[]
					for (let i = 0; i < beforeData.length; i++) {
						if (tempArr.indexOf(beforeData[i].PLACE_CODE) === -1) {
							afterData.push({
								PLACE_CODE: beforeData[i].PLACE_CODE,
								origin: [beforeData[i]]
							})
							tempArr.push(beforeData[i].PLACE_CODE)
						} else {
							for (let j = 0; j < afterData.length; j++) {
								if (afterData[j].PLACE_CODE == beforeData[i].PLACE_CODE) {
									afterData[j].origin.push(beforeData[i])
									break
								}
							}
						}
					}
					function compare(property){
						return function(a,b){
							var value1=a[property]
							var value2=b[property]
							return value1-value2
						}
					}
					for(let i=0;i<afterData.length;i++){
						afterData[i].origin.sort(compare('PLACE_OPTION'))
					}
					vm.arrayList=afterData
					console.log(vm.arrayList)
				}
				vm.isShowMask=false
			})
		}
	},
	mounted(){
    	//配置微信JSSDK参数
        this.common.getsign()
		this.getHomeInfo()
	}
}
</script>

<style lang="stylus" scoped>
.detail
    background #fff
    display flex 
    flex-direction column 
    height 100%
	.main
		flex 1
		overflow-x hidden
		.table_box
			padding: 0 0.3rem;
</style>



