<template>
    <div class="section">
        <div v-if="this.isArrive" class="btn_box">
            <div @click="loading_point('0')" class="btn on">到达装货点扫码</div>
            <div @click="loading_point('1')" class="btn">离开装货点扫码</div>
        </div>
        <div v-if="!this.isArrive" class="btn_box">
            <div @click="loading_point('0')" class="btn">到达装货点扫码</div>
            <div @click="loading_point('1')" class="btn on">离开装货点扫码</div>
        </div>
        <div class="next_box">
            <div class="btn to_next" @click="to_process">已完成所有装货，去往三厂</div>
        </div>
        <!-- 弹出错误提示 -->
        <div class="pop" v-if="popShow">
			<div class="pop_con">
				<div class="pop_info">
                    <span class="iconfont error_icon">&#xe712;</span>
                    <p>{{this.errorText}}</p>
                </div>
				<div class="confirm_box">
					<div @click="hideBox" class="confirm">确定</div>
				</div>
			</div>
			<div class="mask"></div>
		</div>
    </div>
</template>
<script>
import Vue from 'vue'
import Qs from 'qs'
import {mapState} from 'vuex'
export default {
    name:'Btn',
    data(){
        return {
            popShow:false,
            errorText:''
        }
    },
    methods:{
        loading_point:function(num){
            // if(num==0){
            //     this.$store.commit('getArrive',false)
            //     console.log("11")
            // }else if(num==1){
            //     this.$store.commit('getArrive',true)
            //     console.log("22")
            // }
            //微信扫码功能
			wx.scanQRCode({
				needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
				scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
				success: (res) => {
                    console.log(res)
                    let place_no=res.resultStr
                    let now_time=new Date().toLocaleString('chinese',{hour12:false});
					let userName=this.$store.state.userName;
                    let data=Qs.stringify({
                        PLACE_NO:place_no,
                        OPERATE_OPTION:num,
                        CAR_PLATE_NO:Vue.localStorage.get('carNum'),
                        WAYBILL_CODE:Vue.localStorage.get('orderNum'),
                        OPERATOR_NAME:userName,
                        OPERATOR_ID:this.Cookies.get('wxqy_userid'),
                        OPERAT_DATE:now_time
                    })
                    this.axios.post(this.api+'/lyapi/transfer/SCAN_PLACE',data,this.sendHeaders).then(res=>{
                        console.log(res)
                        let data=res.data
                        if(data.RESULT==1){
                            if(num==0){
                                this.$store.commit('getArrive',false)
                            }else if(num==1){
                                this.$store.commit('getArrive',true)
                            }
                            this.$emit('getTime')
                        }else{
                            this.errorText=data.MESSAGE
                            this.popShow=true
                        }
                    })
				}
			})
        },
        to_process(){
			this.$router.push({
                path:'/process'
			})
        },
        hideBox(){
            this.popShow=false
        }
    },
    computed:mapState({
        isArrive:function(state){
			if(state.isArrive===true && Vue.localStorage.get('isArrive')!==null){
				state.isArrive=JSON.parse(Vue.localStorage.get('isArrive'))
            }
			return state.isArrive
		}
    })
}
</script>
<style lang="stylus" scoped>
.section
    padding 0.35rem 0
    background #fff
    border-top 1px solid #eaeaea
    .btn_box
        padding 0 0.3rem
        display flex
        justify-content space-between
        .btn
            pointer-events:none;
        .btn.on
            pointer-events:auto;

    .btn
        width 48%
        height 1rem
        line-height 1rem
        color #fff
        font-size 0.44rem
        font-weight bold
        border-radius 0.1rem
        text-align center
        background #767676
    .btn.on 
        background #FF841E
        
    .next_box
        padding 0 0.3rem
        margin-top 0.5rem
        .to_next
            width 100%
            background-color #cf001f
</style>


