<template>
    <div class="supplier">
        <div class="box">
            <p>部品清单</p>
            <table>
                <tr>
                    <!-- <th>编号</th> -->
                    <th>供应商简称</th>
                    <th>零件编号</th>
                    <th>明示卡数</th>
                </tr>
                <tr v-for="item in list">
                    <!-- <td>{{item.SUPPLY_CODE}}</td> -->
                    <td>{{item.SUPPLY_NAME}}</td>
                    <td>{{item.PART_CODE}}</td>
                    <td>{{item.ORDER_NUMBER}}</td>
                </tr>
            </table>
        </div>
    </div>
</template>
<script>
export default {
    name:'Supplier',
    props:{
        list:Array
    }
}
</script>
<style lang="stylus" scoped>
    .supplier
        padding-bottom 0.3rem
        .box
            p 
                font-size 0.38rem
                padding 0.28rem 0
                font-weight bold
            table
                width 100%
                text-align center
                border-collapse separate
                border-spacing 0
                border 1px solid #eaeaea
                border-radius 0.1rem
                overflow hidden
                tr
                    td,th
                        font-size 0.32rem
                        padding 0.3rem 0
                    th
                        color #ffffff
                        background #a2a2a2
                    td 
                        border-bottom 1px solid #eaeaea
                tr:last-child
                    td
                        border-bottom none

</style>


